<template>
  <div v-title data-title="个人中心-资源">
    <UserNav></UserNav>
    <el-container direction='vertical'>
      <el-main  style="margin-left: 250px;padding: 0px;">
      <div v-for="(file, index) in user.fileList" :key="index" style="margin: 10px;">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            下载文件：<el-button type="plain" @click="download(file)">{{file.name}}</el-button>
            <el-button type="danger" @click="deleteFile(file.id)" v-if="rightUser">删除文件</el-button>
          </div>
          <div class="text item">
            {{file.pub_date}}
          </div>
        </el-card>
      </div></el-main>
    </el-container>
  </div>
</template>

<script>
import UserNav from './UserCenter'
import axiosInstance from "../../api";
export default {
  name: 'UploadedFiles',
  data () {
    return {
      //fileimgurl: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      user: {
        fileList:
            [{// 只有文件1可以下载。图片1直接打开了；图片2找不到图片。
          id:1,
              name: '文件1.txt',
              url: '/static/a.txt',
              pub_date:'2001-01-12'
            },
            {id:2,
              name: '图片1.png',
              url: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png',
              pub_date:'2001-01-12'
            },
            {id:3,
              name: '图片2.png',
              url: '@/assets/logo.png',
              pub_date:'2001-01-12'
            }
            ]
      },
      RealUserId: 0,
      rightUser:true
    }
  },
  methods: {
    download (file) {
      let a = document.createElement('a')
      a.href = file.url
      a.download = file.name
      a.click()
    },
    getFiles(){
      const axios = axiosInstance
      axios.get('http://127.0.0.1:8000/files/',{params:{author:this.RealUserId}}).then(response =>{
        if(response.status === 200){
          let i = 0;
          for(let j in response.data.results){
            this.fileList[i].name=j.name
            this.fileList[i].url = j.file
            this.fileList[i].pub_date = j.pub_date
            i++
          }
        }
      })
    },
    deleteFile(id){
      const axios = axiosInstance
      axios.delete('http://127.0.0.1:8000/files/'+id).then(response => {
        if(response.status === 204){
          this.$message({
            message:'删除成功！',
            type:'success'
          })
        }else {
          this.$message({
            message:'删除失败',
            type:'error'
          })
        }
      })
    }
  },
  components: {
    UserNav
  },
  mounted() {
    this.RealUserId = this.$route.params.RealUserId
    console.log(this.GLOBAL)
    if (this.RealUserId === this.GLOBAL.user_id) {
      this.rightUser = true
    }
    this.getFiles()
  }
}
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>



