<template>
  <div>
    <el-row>
      <el-col>
        <el-card :body-style="{ padding: '20px' }" style="position:fixed;">
          <el-row>
            <el-button style="padding: 0px; line-height: 0px; border: 0px;" @click="toAuthorInfo(author.username)">
              <el-avatar shape="square" :size="100" :fit="'fill'" :src="author.head"></el-avatar>
            </el-button>
          </el-row>
          <el-row><el-button type="text" @click="toAuthorInfo(author.username)">{{author.username}}</el-button></el-row>
          <el-row><el-button plain style="padding: 7px;" :disabled="ifFollowDisabled" @click="toFollow(author.id)">关注</el-button>
          <el-button plain style="padding: 7px;" :disabled="ifDisabled">取消关注</el-button></el-row>
          <el-row style="margin-top: 10px;">
            <el-tooltip content="点赞"  placement="bottom" effect="light" >
              <el-button type="primary" icon="el-icon-thumb" circle :disabled="ifLike" @click="doLike"></el-button>
            </el-tooltip>
            <el-tooltip content="评论" placement="bottom" effect="light">
              <el-button type="primary" icon="el-icon-edit" circle @click="dialog = true"></el-button>
            </el-tooltip>
            <el-tooltip content="取消点赞"  placement="bottom" effect="light">
              <el-button type="danger" icon="el-icon-thumb" circle :disabled="ifCancelLike" @click="cancelLike"></el-button>
            </el-tooltip>
            <el-tooltip content="分享" placement="bottom" effect="light">
              <el-button type="primary" icon="el-icon-share" circle></el-button>
            </el-tooltip>
            <el-tooltip content="删除" placement="bottom" effect="light">
              <el-button type="danger" icon="el-icon-delete" circle></el-button>
            </el-tooltip>
          </el-row>
        </el-card>
      </el-col>
    </el-row>

    <el-drawer
      title="评论"
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="ltr"
      custom-class="demo-drawer"
      ref="drawer"
      size="70%"
      >
      <div class="demo-drawer__content">
        <el-form :model="form" ref="infoForm">
          <el-form-item label="评论：" :label-width="formLabelWidth" prop="content">
            <div class="edit_container" >
              <myEditor v-model="form.content" :isClear="isClear"></myEditor>
            </div>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm">取 消</el-button>
          <el-button type="primary" @click="submitComment(form.content)" :loading="loading">{{ loading ? '提交中 ...' : '提 交' }}</el-button>
        </div><!--$refs.drawer.closeDrawer()-->
      </div>
    </el-drawer>
  </div>
</template>

<script>
import {submitComment, toFollow, Like, getLikeInfo} from '../../api/api'
import axiosInstance from '../../api/index'
import myEditor from '../EditComponents/wangEditor'
export default {
  name: 'AuthorInfo',
  props: {
    author: Object,
    BlogId: Number
  },
  data () {
    return {
      ifDisabled: true,
      ifFollowDisabled: false,
      ifLike: false,
      ifCancelLike: true,
      likeID: '',
      currentDate: new Date(),
      sizeList: ['large', 'medium', 'small'],
      dialog: false,
      loading: false,
      form: {
        content: '',
        author: {
          id: '1',
          head: '',
          username: 'U1'
        }
      },
      formLabelWidth: '80px',
      timer: null,
      isClear: false
    }
  },
  components: {
    myEditor
  },
  mounted () {
    this.getLike()
  },
  methods: {
    handleClose (done) {
      if (this.loading) {
        return
      }
      this.$confirm('确定要提交评论吗？')
        .then(_ => {
          this.loading = true
          this.timer = setTimeout(() => {
            done()
            // 动画关闭需要一定的时间
            setTimeout(() => {
              this.loading = false
            }, 400)
          }, 2000)
        })
        .catch(_ => {})
    },
    cancelForm () {
      this.loading = false
      this.dialog = false
      clearTimeout(this.timer)
    },
    onEditorReady (editor) {
    },
    submitComment (content) {
      // 提交
      // this.$refs.infoForm.validate，这是表单验证
      this.$refs.infoForm.validate((valid) => {
        if (valid) {
          submitComment(this.BlogId, content).then(response => {
            console.log(response)
            if (response.status === 201) {
              this.$message({
                message: '提交成功', type: 'success'})
            } else {
              this.$message({
                message: response.errMsg,
                type: 'error'
              })
            }
          })
        }
      })
    },
    doLike () {
      Like(this.BlogId).then(response => {
        console.log(response)
        if (response.status === 201) {
          this.ifLike = true
          this.ifCancelLike = false
          this.likeID = response.data.id
          this.$message({
            message: '点赞成功', type: 'success'})
        }
      })
    },
    cancelLike () {
      const axios = axiosInstance
      axios.delete('http://127.0.0.1:8000/like/' + this.likeID).then(response => {
        if (response.status === 204) {
          this.ifLike = false
          this.ifCancelLike = true
          this.$message({
            message: '点赞取消成功', type: 'success'})
        } else if (response.status === 404) {
          this.$message({
            message: response.status.message,
            type: 'error'})
        }
      })
    },
    getLike () {
      getLikeInfo().then(response => {
        console.log(response)
        let sum = response.data.length
        let i = 0
        for (i = 0; i < sum; i++) {
          if (response.data[i].post === this.BlogId) {
            this.ifLike = true
            this.ifCancelLike = false
            break
          }
        }
      })
    },
    toAuthorInfo (username) {
      this.$router.push({name: 'UserInfo', params: {UserId: username, RealUserId: this.author.id}})
    },
    toFollow (authorID) {
      toFollow(authorID).then(response => {
        if (response.status === 201) {
          this.ifDisabled = false
          this.ifFollowDisabled = true
          this.$message({
            message: '关注成功', type: 'success'})
        }
      })
    },
    cancelFollow (authorID) {
      const axios = axiosInstance
      axios.delete('http://127.0.0.1:8000/userfavs/' + authorID).then(response => {
        if (response.status === 204) {
          this.ifDisabled = true
          this.ifFollowDisabled = false
          this.$message({
            message: '取消关注成功', type: 'success'})
        } else if (response.status === 404) {
          this.$message({
            message: response.status.message,
            type: 'error'})
        }
      })
    }
  }
}
</script>

<style>
  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .button {
    padding: 0;
    float: right;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
      display: table;
      content: "";
  }

  .clearfix:after {
      clear: both
  }
  .el-row {
    margin-bottom: 0px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
  .el-drawer{

    overflow: scroll
    }
</style>



