        id: 100,
        author: {
          username: 'User',
          id: '14',
          head: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png'
        },
        title: 'title',
        excerpt: '文章简介', // 无
        pub_date: '2019-01-01',
        tags: [// 无
          {value: 'HTML', label: 'HTML'},
          {value: 'java', label: 'java'}
        ]
      }, {
        id: 200,
        author: {
          username: 'User',
          id: '14',
          head: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png'
        },
        title: 'title',
        excerpt: '文章简介',
        pub_date: '2019-01-01',
        tags: [
          {value: 'HTML', label: 'HTML'},
          {value: 'java', label: 'java'}
        ]
      }],
      total: 2
    }
  },
  components: {
    Page,
    myCard
  },
  inject: ['reload'],
  mounted () {
    this.getCategory()
    this.keys = this.$route.query.keys
    this.getSearchResult(this.currentPage, this.pageSize)
  },
  updated () {
    // this.getSearchResult(this.currentPage, this.pageSize)
  },
  watch: {// 用来每次重新输入关键词都可以重新查询
    '$route': {
      handler (route) {
        const that = this
        if (route.name === 'SearchResult') {
          this.getSearchResult(this.currentPage, this.pageSize)
        }
      },
      deep: true
    },
    currentPage: {
      handler (oldValue, newValue) {
        if (oldValue !== newValue) {
          this.getSearchResult(this.currentPage, this.pageSize)
        }
      }
    },
    pageSize: {
      handler (oldValue, newValue) {
        if (oldValue !== newValue) {
          this.getSearchResult(this.currentPage, this.pageSize)
        }
      }
    },
    check: {
      handler (oldValue, newValue) {
        if (oldValue !== newValue) {
          this.getSearchResult(this.currentPage, this.pageSize)
        }
      }
    },
    selectValue: {
      handler (oldValue, newValue) {
        if (oldValue !== newValue) {
          this.getSearchResult(this.currentPage, this.pageSize)
        }
      }
    }
  },
  methods: {
    getSearchResult (p, pageSize) {
      this.keys = this.$route.query.keys
      const axios = axiosInstance
      axios.get('http://127.0.0.1:8000/list/', {
        params: {
          category: this.check,
          ordering: this.selectValue,
          search: this.keys,
          p: p,
          page_size: pageSize
        }
      }).then(response => {
        if (response.status === 200) {
          console.log(response)
          this.Cards = response.data.results
          this.total = response.data.count
        } else if (response.status === 404) {
          this.$message({
            message: 'No more things',
            type: 'error'
          })
        }
      })
    },
    updateSelectValue () {
      // this.getSearchResult(this.currentPage, this.pageSize)
      // this.reload()
    },
    updateCheck () {
      // this.getSearchResult(this.currentPage, this.pageSize)
      // this.reload()
    },
    getCategory () {
      getCategory().then(response => {
        if (response.status === 200) {
          let i = 0
          let sum = response.data.length
          for (i = 0; i < sum; i++) {
            this.categories[i].name = response.data[i].name
            this.categories[i].id = response.data[i].id
          }
        }
      })
    },
    toViewMain (ID) {
      this.$router.push({path: '/ViewMain', query: {BlogId: ID}})
    },
    toAuthorInfo () {
      this.$router.push({name: 'UserInfo', params: {UserId: this.AuthorName}})
    },
    changePage (val) {
      this.pageSize = val
      console.log(this.pageSize)
    },
    changeP (val) {
      this.currentPage = val
      console.log(this.currentPage)
    }
  }
}
</script>

<style>
#search {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
